*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.register-pic{
  background:linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url("https://images.pexels.com/photos/1001682/pexels-photo-1001682.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
  background-position:  center;
  background-repeat: no-repeat;
  background-size: cover;
}
.login-pic{
  background:linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url("https://images.pexels.com/photos/1249586/pexels-photo-1249586.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
  background-position:  center;
  background-repeat: no-repeat;
  background-size: cover;
}
.input-name{
  background:transparent;
  padding: 14px;
}
/* .content{
  height: 100vh;
} */

.btn-danger{
  background-color: #ff0000 !important;
  border: 0;
  margin-left: 20px;
}
.text-gap{
margin-top: 5px;
box-sizing: content-box;
}
.register-page{
display: flex;
align-items: center;
justify-content: center;
height: 90vh;
}
.login-page{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
  }

  /* transection page */

  .filters{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 20px;
    box-shadow: 0 0 3px #808080;
    margin-bottom: 10px;
  }
.footer{
  height: auto;
}

  .anticon svg {
    font-size: 20px;
    cursor: pointer;
  }
  .active-icon{
    color: #000;
  }

  .inactive-icon{
    color: #808080;
  }

  .filter-tab .ant-select-selector {
    width: 150px !important;
  }

  .switch-icons{
    border: 1px solid rgba(0, 0, 0, 0.677);
    border-radius: 5px;
    padding: 10px 15px;
  }

  div .deep-effect:hover{
    transform: scale(1.03);
    transition: all .3s ease-in-out;
    border-radius: 4px;
  }

  div .bg-danger{
    background-color: #f61a1a !important;
  }


@media only screen and (max-width: 767px) {
.container-fluid ul li{
  width: 100%;
  text-align: center;
}
.container-fluid ul li button{
  width: 200px;
}

.content{
  overflow-y: scroll;
}

.filters{
  flex-wrap: nowrap;
}
.item {
  width: calc(100vw - 20px); /* Adjust the width to fit mobile screen */
  margin-right: 0; /* Remove margin between items */
  padding-left: 5px;
}
}

.symbol{
  color: #fff;
  font-size: 50px;
  text-align: center;
  padding-bottom: 0;
  margin-bottom: 0;
  padding-top:5px;
  background-image: url("https://images.pexels.com/photos/1883386/pexels-photo-1883386.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
  background-size: cover;
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}